import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { selectUser, selectUserRole } from '../../redux/userReducer';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

const FAQ = () => {
  const navigate = useNavigate();
  const seluser = useSelector(selectUser);
  const userRole = useSelector(selectUserRole);

  return (
    <>
    {userRole === 'merchant' ? <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Frequently Asked Questions</p>
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>:
    <Layout classes={'bg-[#404040] h-full'} searchBar={false} walletNav={false}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Frequently Asked Questions</p>
              </div>
            </div>
        </div>
    </Layout>}
    </>
  );
}

export default FAQ;